/* colors */
.navbar {
  margin-top: 0.745pt;
  text-align: center;
  font-family: "Neaue-Singular-regular", sans-serif;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  width: 100%;
  height: 0;
  z-index: 1;
  display: flex;
}

.navbar__wrapper {
  display: flex;
  justify-content: center;
}

.navbar__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 1.1em;
}

.nav__logo {
  color: #2e3047;
  cursor: pointer;
  text-decoration: none;
  flex-grow: 1;
  font-size: 1.2rem;
  width: 20%;
}

.nav-menu {
  list-style: none;
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-content: center;
  margin: 0;
}

.nav-links {
  color: #2e3047;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
  font-size: 0.98rem;
  align-self: center;
}

.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #2e3047;
}

.navbar__icon-wrapper {
  display: none;
}

@media screen and (max-width: 766px) {
  /* exit */
  .nav-menu {
    display: flex;
    padding-top: 0;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 5em;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  /* enter */
  .nav-menu.active {
    background: #eadbcd;
    filter: opacity(120%);
    top: 5em;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    height: fit-content;
  }
  .nav-item .active {
    color: #2e3047;
    border: none;
  }
  .nav-links {
    padding: 1rem;
    width: 100%;
    display: table;
    color: #2e3047;
    margin: 0;
  }
  .navbar__icon-wrapper {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    color: #2e3047;
  }
  .navbar__icon-wrapper .icon {
    cursor: pointer;
  }
}
/* Button 1 */
