  animation: rotating 7s linear infinite;
}
.hero .hero__wrapper .hero__contact:hover {
  animation-play-state: paused;
}

/* keyframes */
@keyframes rotating {
  from {
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg);
    transform-style: preserve-3d;
    will-change: auto;
  }
  to {
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(360deg) skew(0deg);
    transform-style: preserve-3d;
    will-change: auto;
  }
}
